/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: "";
  content: none; }

q:before, q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.home {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden; }
  .home > h1 {
    font-family: "Anton";
    font-size: 8vh;
    text-transform: uppercase; }
  .home > p {
    font-family: "Roboto";
    font-size: 5vh;
    line-height: 6vh;
    padding: 3%; }
    .home > p.congrat {
      font-size: 6vh; }
      .home > p.congrat span {
        display: block;
        padding: 3%; }
  .home > div {
    padding: 5% 0;
    width: 70%;
    background: #d0021b;
    justify-self: center; }
    .home > div a {
      text-decoration: none;
      color: white;
      font-family: "Roboto";
      font-size: 3vh; }

.navigation {
  height: 25%;
  width: 80%;
  background: rgba(0, 0, 0, 0.6);
  grid-row: 3/-1;
  justify-self: center;
  align-self: flex-start;
  justify-content: space-around;
  align-items: center;
  border-radius: 7%;
  margin-top: 15%;
  font-family: "Roboto";
  font-weight: 900; }
  .navigation ul {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center; }
  .navigation li {
    height: 100%;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center; }
    .navigation li:first-child {
      border-top-left-radius: 7%;
      border-bottom-left-radius: 7%; }
    .navigation li:last-child {
      border-top-right-radius: 7%;
      border-bottom-right-radius: 7%; }
  .navigation .active {
    background: #d0021b; }

.non-stop {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 1fr;
  justify-content: center;
  align-content: center;
  align-items: center; }
  .non-stop > div {
    width: 100%; }
  .non-stop h3 {
    text-align: center;
    text-transform: uppercase;
    font-family: "Roboto";
    font-weight: 900;
    font-size: 5vh; }
  .non-stop .btnContainer {
    display: flex;
    justify-content: space-between;
    margin: 20% 0%;
    padding: 0 5%;
    font-size: 5vh;
    font-family: "Roboto";
    font-weight: 600; }

.interval {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: 1fr;
  justify-content: center;
  align-content: center;
  align-items: center;
  padding-top: 10%; }
  .interval > div {
    width: 100%; }
  .interval h3 {
    text-align: center;
    text-transform: uppercase;
    font-family: "Roboto";
    font-weight: 900;
    font-size: 3vh; }
  .interval .btnContainer {
    display: flex;
    justify-content: space-between;
    margin: 10% 0%;
    padding: 0 5%;
    font-size: 3vh;
    font-family: "Roboto";
    font-weight: 600; }

.settings {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 10vh auto 30vh;
  justify-content: center;
  overflow: hidden; }
  .settings .card {
    grid-row: 2/3;
    height: 63vh;
    width: 80vw;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 7%;
    padding: 5%; }
    .settings .card .setBtn {
      background: #d0021b;
      width: 15vw;
      text-align: center;
      border-radius: 10%; }
  .settings .goTimer {
    display: flex;
    justify-self: right;
    align-content: center;
    align-items: center;
    height: 45px;
    width: 45px;
    text-decoration: none;
    color: white;
    font-size: 3vh;
    font-weight: 900;
    font-family: "Roboto"; }
    .settings .goTimer img {
      width: 30px;
      height: 30px;
      object-fit: contain;
      margin-right: 20%; }

.top {
  grid-row: 1/2;
  background: rgba(0, 0, 0, 0.6);
  -webkit-clip-path: ellipse(70% 100% at 50% 0%);
          clip-path: ellipse(70% 100% at 50% 0%);
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr); }

.display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .display > span {
    margin: 3%; }
    .display > span:first-child {
      font-size: 3vh; }
    .display > span:last-child {
      font-size: 4vh; }

.sets {
  grid-row: 1/2;
  grid-column: 1/2; }

.time {
  grid-row: 1/-1;
  grid-column: 2/3;
  text-align: center; }
  .time > span {
    margin: 7%; }
    .time > span:first-child {
      font-size: 5vh;
      font-weight: 600; }
    .time > span:last-child {
      font-size: 10vh;
      margin-right: 1%; }
      .time > span:last-child > span {
        font-size: 6vh; }

.cycles {
  grid-row: 1/2;
  grid-column: 3/-1; }

.workout {
  color: #d0021b; }

.rest {
  color: #111faa; }

.totalTime {
  grid-row: 2/3;
  text-align: center;
  margin-top: 5%; }
  .totalTime > p {
    font-size: 8vh;
    font-weight: 600;
    margin-bottom: 5%; }
  .totalTime > span {
    font-size: 6vh;
    font-weight: 100; }

.pauseBtnContainer {
  grid-row: 3/4;
  grid-column: 1/-1; }
  .pauseBtnContainer .pauseBtn {
    height: 100%;
    -webkit-clip-path: circle(25% at 50% 50%);
            clip-path: circle(25% at 50% 50%);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 5vh;
    font-weight: 600; }

.isPaused {
  background-color: #d0021b; }

.isPlayed {
  background-color: #0aa313; }

.timerDisplay {
  height: 100vh;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  font-family: "Roboto", sans-serif; }

.settingsBtn {
  grid-row: 4/-1;
  text-align: center;
  justify-self: center;
  align-self: center;
  background: rgba(0, 0, 0, 0.6);
  text-decoration: none;
  color: #d0021b;
  padding: 3vh 10vh;
  font-size: 4vh;
  font-weight: 600; }

body {
  background: -webkit-linear-gradient(left top, rgba(48, 48, 48, 0.7), rgba(57, 11, 11, 0.9)), url(/pwa-timer/static/media/PHOTO-2018-12-21-22-44-00.692378c5.jpg) no-repeat center center/cover;
  background: linear-gradient(to right bottom, rgba(48, 48, 48, 0.7), rgba(57, 11, 11, 0.9)), url(/pwa-timer/static/media/PHOTO-2018-12-21-22-44-00.692378c5.jpg) no-repeat center center/cover;
  height: 100vh;
  color: white;
  overflow: hidden; }

